









































export default {
	data() {
		return {
			status: true,
			list:''
		};
	},
	onShow() {
		this.get(2)
	},
	methods:{
		set(){
			if(this.status){
				this.get(1)
			}else{
				this.get(2)
			}
			this.status = !this.status
		},
		get(status){
			var that = this;
			uni.showLoading({
				title: '加载中...'
			})
			uni.request({
				url: that.http + 'order/getYuyue',
				method: 'POST',
				header: that.header,
				data: {
					user_id: uni.getStorageSync('token'),
					status:status
				},
				success: (res) => {
					uni.hideLoading()
					for(var i in res.data.data){
						res.data.data[i].yue_time =new Date(parseInt(res.data.data[i].yue_time) * 1000).toLocaleString().replace(/:\d{1,2}$/,' ')
					}
					that.list = res.data.data
					console.log(res)
				},
				fail() {
					uni.showToast({
						title: "无法查询是否有订单正在进行，请刷新后重试!",
						icon: 'none'
					})
				}
			})
		}
	}
}
